import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import _ from 'lodash'
import { getDefines, getStatusContract, getContractDetail } from '../../apis/contractForm'
import { pageStatusPermission } from '../../ultils'

export const fetchDefines = createAsyncThunk(
  'form-contract/get-defines',
  async (thunkAPI) => {
    const response = await getDefines()
    return response.data
  }
)

export const fetchStatusContract = createAsyncThunk(
  'form-contract/get-status',
  async (thunkAPI) => {
    const response = await getStatusContract()
    return response.data
  }
)

export const fetchDetail = createAsyncThunk(
  'form-contract/get-detail',
  async (id,thunkAPI) => {
    const response = await getContractDetail(id);
    let result = "";
    if (_.isEmpty(response.data) && response.code == "401") {
      pageStatusPermission(401);
    } else {
      result = response.data;
    }
    return result;
  }
)

const initialState = {
  infoContract: {
    "contractId": null,
    "contractCode": null, // S H  gen
    "customerName": null,
    "customerIdCard": null, // CMND or CCCD
    "customerIdIssueDate": null, // ngy cp CMND/CCCD 
    "customerIdIssuePlace": null, // Ni cp
    "partnerName": null, // Tn ca V hoc Chng
    "partnerIdCard": null,
    "partnerIdIssueDate": null,
    "partnerIdIssuePlace": null,
    "customerAddress": null,
    "customerBankAccountNumber": null, // S TK ngn hng
    "bankId": null, // id ca ngn hng trong h thng Propzy
    "customerPhone": null,
    "customerEmail": null,
    "isExportReceipt": false, // Yu cu xut ha n GTGT
    "propertyAddress": null, // ia ch ca property
    "listingAddress":null,
    "propertyNumberGcn": null, // S GCN
    "propertyNumberGcnBook": null, // S vo s cp GCN
    "propertyNumberGcnIssuePlace": null, // Ni cp GCN
    "propertyNumberGcnIssueDate": null, // Ngy cp GCN
    "propertyRegisDateChanged": null, // ng k thay i ngy
    "propertyFeeNotify": null, // Thng bo np l ph trc b s
    "propertyFeeDate": null, // Ngy cp thng bo np l ph trc b
    "propertyContractNumber": null, // Hp ng mua bn s
    "signDate": null,
    "signBetween": null, // K gia ...
    "signWith": null, // K vi ...
    "serviceTypeId": 2, // Loi dch v
    "servicePackageId": null, // Gi dch v
    "servicePackageChildIds": [],
    "commissionTypeId": 21, // Loi hoa hng
    "commission": null, // Gi tr hoa hng nu type l %, nu type l VN th  null
    "commissionValue": null, // Value ca hoa hng, trong trng hp loi hoa hng l % th FE t tnh ra s tin VND da theo propertyPrice
    "negotiationPrice": null,
    "contractDuration": null, // Thi hn hp ng (tnh bng thng)
    "paymentTermId": null, // Thi hn thanh ton
    "paymentMethodId": null, // Hnh thc thanh ton
    "serviceTimeId": null, // Thi gian thc hin dch v
    "contractValidDate": null, // Ngy hiu lc hp ng
    "statusId": 2, // Trng thi hp ng,
    "lsoId": null
  },
  types: [],
  loaded:{
    define:false,
    status:false,
    info:false
  }
}

const slice = createSlice({
    name: 'formContract',
    initialState: initialState,
    reducers: {
      fetch(state, action) {
        console.log("fetch", "state", state, "action", action)
      }
    },
    extraReducers: (builder) => {
      // Add reducers for additional action types here, and handle loading state as needed
      builder.addCase(fetchDefines.fulfilled, (state, action) => {
        state.types = state.types.concat(action.payload);
        state.loaded.define = true;
      })
      .addCase(fetchStatusContract.fulfilled, (state, action) => {
        state.types = state.types.concat(action.payload);
        state.loaded.status = true;
      })
      .addCase(fetchDetail.fulfilled, (state, action) => {
        state.infoContract = action.payload
        state.loaded.info = true;
      })
    },
  })

export const { fetch } = slice.actions

export default slice.reducer