import { useState } from "react";
import { useSelector } from "react-redux";

const useTypes = () => {
    const types = useSelector(state=>state.formContractSlice.types);
    // console.log(typesSelector);
    // const [types] = useState([{"code":"SERVICE_TYPE","name":"Loi dch v","list":[{"id":2,"code":"SERVICE_TYPE_SELL","name":"Bn","control":"Radio","unsignedName":"","childList":[]},{"id":3,"code":"SERVICE_TYPE_RENT","name":"Thu","control":"Radio","unsignedName":"","childList":[]}]},{"code":"SERVICE_PACKAGE","name":"Gi dch v","list":[{"id":5,"code":"SERVICE_PACKAGE_IBUY","name":"IBUY","control":"Radio","unsignedName":"","childList":[{"id":6,"code":"SERVICE_PACKAGE_IBUY_PROP_POST","name":"ng tin BS","control":"Checkbox","unsignedName":"","childList":[]},{"id":7,"code":"SERVICE_PACKAGE_IBUY_PROP_VAL","name":"Thm nh gi BS","control":"Checkbox","unsignedName":"","childList":[]},{"id":8,"code":"SERVICE_PACKAGE_IBUY_SELL_TO_PROPZY","name":"Bn cho Propzy","control":"Checkbox","unsignedName":"","childList":[]}]},{"id":9,"code":"SERVICE_PACKAGE_EXCLUSIVE","name":"c quyn","control":"Radio","unsignedName":"","childList":[{"id":10,"code":"SERVICE_PACKAGE_EXCLUSIVE_PROP_POST","name":"ng tin BS","control":"Checkbox","unsignedName":"","childList":[]},{"id":11,"code":"SERVICE_PACKAGE_EXCLUSIVE_PROP_VAL","name":"Thm nh gi BS","control":"Checkbox","unsignedName":"","childList":[]},{"id":12,"code":"SERVICE_PACKAGE_EXCLUSIVE_PROP_CONSULT","name":"T vn, t lch xem BS","control":"Checkbox","unsignedName":"","childList":[]}]},{"id":13,"code":"SERVICE_PACKAGE_STANDARD","name":"Tiu chun","control":"Radio","unsignedName":"","childList":[{"id":14,"code":"SERVICE_PACKAGE_STANDARD_PROP_POST","name":"ng tin BS","control":"Checkbox","unsignedName":"","childList":[]},{"id":15,"code":"SERVICE_PACKAGE_STANDARD_PROP_CONSULT","name":"T vn, t lch xem BS","control":"Checkbox","unsignedName":"","childList":[]}]}]},{"code":"PAYMENT_TERM","name":"Thi hn thanh ton","list":[{"id":17,"code":"PAYMENT_TERM_100","name":"100 % ph dch v ngay khi Giao dch thnh cng.","control":"Radio","unsignedName":"","childList":[]},{"id":18,"code":"PAYMENT_TERM_80","name":"80% ngay khi t cc, 20% cn li thanh ton sau khi k hp ng mua bn/ Cng chng sang tn","control":"Radio","unsignedName":"","childList":[]}]},{"code":"COMMISSION_TYPE","name":"Loi hoa hng","list":[{"id":20,"code":"COMMISSION_TYPE_BY_MONEY","name":"Bng tin","control":"Dropdown","unsignedName":"","childList":[]},{"id":21,"code":"COMMISSION_TYPE_PERCENT","name":"Theo phn trm %","control":"Dropdown","unsignedName":"","childList":[]}]},{"code":"PAYMENT_METHOD","name":"Hnh thc thanh ton","list":[{"id":23,"code":"PAYMENT_METHOD_BANK_TRANSFER","name":"Chuyn khon","control":"Radio","unsignedName":"","childList":[]},{"id":24,"code":"PAYMENT_METHOD_CASH","name":"Tin mt","control":"Radio","unsignedName":"","childList":[]}]},{"code":"SERVICE_TIME","name":"Thi hn thc hin dch v","list":[{"id":26,"code":"SERVICE_TIME_30","name":"30 ngy","control":"Radio","unsignedName":"","childList":[]},{"id":27,"code":"SERVICE_TIME_60","name":"60 ngy","control":"Radio","unsignedName":"","childList":[]},{"id":28,"code":"SERVICE_TIME_90","name":"90 ngy","control":"Radio","unsignedName":"","childList":[]}]},{"code":"CUSTOMER_RELATION","name":"Mi quan h","list":[{"id":31,"code":"CUSTOMER_RELATION_PARTNER","name":"V/chng","control":"","unsignedName":"","childList":[]}]},{"code":"BANK","name":"Ngn hng","list":[{"id":1,"code":"ACB","name":"Ngn hng thng mi c phn  Chu","unsignedName":"ngan hang thuong mai co phan a chau","childList":[]},{"id":2,"code":"BIDV","name":"Ngn hng u t v Pht trin Vit Nam","unsignedName":"ngan hang dau tu va phat trien Viet Nam","childList":[]},{"id":3,"code":"Sacombank","name":"Ngn hng thng mi c phn Si gn Thng Tn","unsignedName":"ngan hang thuong mai co phan sai gon thuong tin","childList":[]}]},{"code":"STATUS","name":"Trng thi","list":[{"id":2,"code":"STATUS_CONTRACT","name":"Hp ng","control":null,"unsignedName":"Hop ong","childList":[]},{"id":3,"code":"STATUS_DRAFT","name":"Draft","control":null,"unsignedName":"Draft","childList":[]}]}])
    const getType = code => {
        return types.map((type) => code === type.code ? type : null).filter(item => item !== null)[0];
    }

    const getTypeReactSelect = code => {
        const type = getType(code);
        if(!type?.list){
            return [];
        }
        return type.list.map(item => {
            let codeLabel = item.code !== null ? `(${item.code})` : '';
            let label = code === 'BANK' ? `${item.name} ${codeLabel}` : item.name;
            return { "value": item.id, "label": label };
        })
    }

    const getDefaultValue = (code, value) => {
        const type = getType(code);
        if(!type?.list){
            return [];
        }
        return type.list.map((item) => {
            let codeLabel = item.code !== null ? `(${item.code})` : '';
            let label = code === 'BANK' ? `${item.name} ${codeLabel}` : item.name;
            // let label = code === 'BANK' ? `${item.code} - ${item.name}` : item.name;  
            return value === item.id ? { "value": item.id, "label": label } : null
        }).filter(item => item !== null)[0]
    }

    return [getTypeReactSelect, getType, getDefaultValue];
}

export default useTypes;